.flickerImg {
}

.flickerImg-enter {
  animation-name: flicker, shake;
  animation-duration: .5s, .7s;
  animation-timing-function: linear, ease-in-out;
}

.flickerImg-enter-active {
}

.flickerImg-exit {
  animation-name: flicker, shake;
  animation-duration: .5s, .7s;
  animation-timing-function: linear, ease-in-out;
}

.flickerImg-exit-active {}


@keyframes flicker {
  from {
    opacity: .2;
    transform: rotateY(0deg);
  }
  4% {
    opacity: 1;
  }
  6% {
    opacity: .5;
  }
  8% {
    opacity: .5;
  }
  10% {
    opacity: .5;
  }
  12% {
    opacity: .5;
  }
  14% {
    opacity: .5;
  }
  16% {
    opacity: .2;
  }
  18% {
    opacity: .2;
  }
  20% {
    opacity: .2;
  }
  22% {
    opacity: .5;
  }
  24% {
    opacity: .5;
  }
  26% {
    opacity: .6;
  }
  28% {
    opacity: .8;
  }
  30% {
    opacity: .8;
  }
  38% {
    opacity: .5;
  }
  48% {
    opacity: .6;
  }
  50% {
    opacity: .5;
  }
  56% {
    opacity: .4;
  }
  58% {
    opacity: .5;
  }
  60% {
    opacity: .4;
  }
  68% {
    opacity: .6;
  }
  70% {
    opacity: .5;
  }
  72% {
    opacity: .7;
  }
  93% {
    opacity: .8;
  }
  95% {
    opacity: .9;
  }
  97% {
    opacity: .9;
  }
  to {
    opacity: 0;
    transform: rotateY(360deg);
  }
}

.fade {}

.fade-enter {
  opacity: 0;
  transform: scale(.5);
}

.fade-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: .3s;
}

.fade-exit {
  opacity: .5;
  transform: scale(1);
}

.fade-exit-active {
  opacity: 0;
  transform: scale(.5);
  transition: .3s;
}

.animationInOut-enter {
  animation: animationIn .8s;
}

.animationInOut-exit {
  animation: animationOut .8s;
}

.logoWrap-enter {
  animation: logoW 1s reverse;
}

.logoWrap-exit {
  animation: logoW 1s;
}

.showMenu-enter {
  animation: shake 1s reverse;
}

.showMenu-exit {
  animation: shake 1s;
}

.like {
  cursor: pointer;
  width: 50px;
  height: 50px;
  /* background-size: cover; */
  background-image: url('https://static.platzi.com/media/files/corazon_b33c879a-0926-4897-a92f-495f0a44018c.png');
  background-size: 1450px 50px;
  bottom: -100vh;
  position: fixed;
}
.like.is-liked {
  position: absolute;
  animation-name: like, move;
  animation-duration: .8s, 5s;
  /* 2900 / 29 = 100px */
  animation-timing-function: steps(28), linear;
  /* background-position : right; */
  animation-fill-mode: forwards;
  animation-iteration-count: infinite, 1;
}

@keyframes move {
  0% {
    bottom: 0;
  }
  100% {
    bottom: 120%;
  }
}

@keyframes like {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: right;
    /* background-position: -2800px 0; */
  }
}

@keyframes logoW {
  0% {
    opacity: 1;
    transform: rotate(360deg);
  }
  100% {
    opacity: 0;
    transform: rotate(0deg);
  }
}

@keyframes animationIn {
  0% {
    transform: translateY(-3000px);
  }
  60% {
    transform: translateY(30px);
  }
  70% {
    transform: translateY(-10px);
  }
  90% {
    transform: translateY(5px);
  }
}

@keyframes animationOut {
  0% {
    transform: translateY(5px);
  }
  60% {
    transform: translateY(-10px);
  }
  75% {
    transform: translateY(30px);
  }
  100% {
    transform: translateY(-3000px);
  }
}

@keyframes shake {
  from, 90% {
    transform: translateX(-10px);
  }
  20%, 80% {
    transform: translateX(2px);
  }
  30%, 50%, 70% {
    transform: translateX(-4px);
  }
  40%, 60% {
    transform: translateX(4px);
  }
}
